








import {
  defineComponent, ref, watch,
} from '@vue/composition-api';

import RichText from '@/components/rte/main.vue';

export default defineComponent({
  components: {
    RichText,
  },
  props: {
    value: {
      type: String,
      default: '',
    },
  },
  setup(props) {
    const rText = ref('');
    watch(() => props.value, (v) => {
      rText.value = v;
    }, {
      immediate: true,
    });
    return {
      rText,
    };
  },
});
