









import {
  defineComponent, ref, watch, toRefs, computed,
} from '@vue/composition-api';
import { fetchMaterialTypes } from '@/api/material';
import useLoader from '_/composables/use-loader';
import { ElCascader } from 'element-ui/types/cascader';
import translateCategoryName from '@/utils/i18n-material-type';

type MaterialSelectItem = {
  id: string,
  name: string,
  label: string,
  subCount?: number,
  children?: MaterialSelectItem[]
  parentIds?: string[]
  leaf?: boolean,
  disabled?: boolean,
};

export default defineComponent({
  model: {
    prop: 'value',
    event: 'change',
  },
  props: {
    strictMode: {
      type: Boolean,
      default: false,
    },
    maxLevel: {
      type: Number,
      default: 2,
    },
    value: {
      type: Array as () => string[],
      required: true,
      default: () => [],
    },
    checkStrictly: {
      type: Boolean,
      default: false,
    },
    expandTrigger: {
      type: String,
      default: 'click',
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    showClearable: {
      type: Boolean,
      default: true,
    },

    businessType: {
      // 1安踏 0普通
      type: Number,
      default: 1,
    },
  },
  setup(props, { emit }) {
    const selected = ref<MaterialSelectItem[]>([]);

    const cascader = ref<ElCascader>();

    async function getChildren(id: string, level: number) {
      const list = (await fetchMaterialTypes(id, props.businessType)) as MaterialSelectItem[];
      return list.map(i => {
        i.leaf = !(i.subCount && i.subCount > 0 && level < props.maxLevel);
        i.disabled = props.maxLevel !== level && !i.subCount && props.strictMode;
        if (props.businessType === 0) {
          i.label = translateCategoryName(i.name);
        } else {
          i.label = i.name;
        }
        return i;
      });
    }

    async function initOptionList(children: MaterialSelectItem[], list: string[]) {
      const result = [];
      while (children && children.length) {
        // eslint-disable-next-line no-loop-func
        const item = children.find(j => j.id === list[result.length]);
        if (item && result.length < props.maxLevel) {
          result.push(item);
          item.children = item.children || await getChildren(item.id, result.length);
          ({ children } = item);
        } else {
          children = [];
        }
      }
      return result;
    }

    const checkedNodes = computed(() => {
      const nodes = cascader.value?.getCheckedNodes()[0]?.pathNodes?.map(n => {
        return n.data;
      }) || [];
      return nodes;
    });

    const getCheckedNodes = () => {
      const nodes = cascader.value?.getCheckedNodes()[0]?.pathNodes?.map(n => {
        return n.data;
      }) || [];
      return nodes;
    };

    const cascaderProps = {
      lazy: true,
      label: 'label',
      value: 'id',
      children: 'children',
      checkStrictly: props.checkStrictly,
      expandTrigger: props.expandTrigger,
      lazyLoad: async (node, resolve) => {
        if (node.level && node.level < props.maxLevel + 1 && !node.leaf && node.children?.length === 0) {
          const res = await getChildren(node.value, node.level);
          resolve(res);
        } else {
          resolve();
        }
      },
    };

    const list = useLoader(async ({ value }) => {
      const defaultList = await getChildren('-1', 0);
      if (value.length) {
        if (defaultList) {
          selected.value = await initOptionList(defaultList, value);
        }
      }
      return defaultList;
    }, {
      ...toRefs(props),
    }, {
      initialValue: [],
    });

    async function handleChange(ids: string[]) {
      if (!cascader.value?.getCheckedNodes()[0]) {
        emit('change', ids, []);
      } else {
        const nodes = cascader.value?.getCheckedNodes()[0]?.pathNodes?.map(n => {
          return n.data;
        }) || [];
        emit('change', ids, nodes);
      }
    }

    watch(() => checkedNodes.value, (v) => {
      if (v.length > 0) handleChange(props.value);
    });
    // watch(() => {
    //   return props.value;
    // }, (v, ov) => {
    //   if (JSON.stringify(v) !== JSON.stringify(ov)) {
    //     if (v.length > 0) {
    //
    //       initOptionList(list.value, v);
    //     } else {
    //
    //
    //     }
    //   }
    // });

    return {
      cascaderProps,
      handleChange,
      list,
      cascader,
      getCheckedNodes,
      checkedNodes,
    };
  },

});
