<template>
  <div class="attachment-image">
    <h2 class="attachment-image-title" :style="titleStyle">
      <span>{{ $t('material.info.attachment_images_caption') }}</span>
      <el-popover
        trigger="hover"
        width="400"
        placement="bottom-start">
        <IconQuestion slot="reference" />
        <div>
          {{ $t('material.info.attachment_images_tip') }}<br>
          {{ $t('material.info.attachment_images_tip2') }}
        </div>
      </el-popover>
    </h2>
    <UploadWithPreviewer
      ref="uploadWithPreviewer"
      :urls="paths"
      :maxSize="3 * 1024 * 1024"
      :width="78" :height="78"
      accept=".png, .jpeg, .jpg"
      multiple
      :disabled="disabled"
      @change="changeImage" />
  </div>
</template>

<script>
import uploadToOSS from '@/utils/oss/uploadToOSS';

import UploadWithPreviewer from '@/components/upload/UploadWithPreviewer';
import IconQuestion from '@/components/icon/IconQuestion';

export default {
  components: {
    UploadWithPreviewer,
    IconQuestion,
  },
  props: ['assetsInfo', 'titleStyle', 'disabled'],
  data() {
    return {
      files: [],
      paths: [], // 记录已上传的oss地址
    };
  },
  watch: {
    assetsInfo: {
      handler(obj) {
        if (obj && obj?.images) {
          this.paths = obj?.images;
          this.files = this.paths.map(path => {
            return { file: '', url: path };
          });
        }
      },
      immediate: true,
    },
  },
  methods: {
    // 获取上传文件oss地址的列表（外部调用）
    async getImageOssPaths() {
      const result = this.$refs.uploadWithPreviewer.fileList.reduce((acc, cur) => {
        if (cur.file) {
          acc.uploading.push(cur.file);
        } else {
          acc.uploaded.push(cur.url);
        }
        return acc;
      }, { uploaded: [], uploading: [] });
      const uploadedPaths = result.uploaded.map(url => url.split('?')[0].split('/').slice(-2).join('/'));
      const paths = await Promise.all(result.uploading.map(file => uploadToOSS(file, 'MATERIAL_ASSETS')));
      return [...uploadedPaths, ...paths];
    },

    changeImage(e) {
      this.files = e;
      this.$closest('MaterialInfo') && (this.$closest('MaterialInfo').saveBtnDisabled = false);
    },
  },
};
</script>

<style lang="scss" scoped>
.attachment-image-title {
  margin-bottom: 10px;
  line-height: 20px;
  font-size: 14px;
  font-weight: 400;
  color: #606060;
}
</style>
