<template>
  <div class="upload-width-previewer">
    <!-- 展示文件框 -->
    <ul class="flex">
      <li
        v-for="(file, i) in fileList"
        :key="i"
        class="file-list-item"
      >
        <DeletableBox
          :width="width"
          :height="height"
          :disabled="disabled"
          @delete="handleDeleteFile(i)"
          @preview="handlePreviewImage(file.url)"
        >
          <div class ="upload-video-item">
            <video :src="file.url" width="100%" height="100%" ref="videoPlayer" />
            <span class="video-play-icon"></span>
          </div>
        </DeletableBox>
      </li>
    </ul>

    <UploadArea
      v-if="fileList.length < max"
      :multiple="multiple"
      :accept="accept"
      :max-size="maxSize"
      :disabled="disabled"
      @upload="handleUploadFile"
    >
      <div
        class="upload-area-box"
        :style="{ width: `${width}px`, height: `${height}px` }"
      >
        <i class="iconfont icon-upload-cloud-o" />
        <p class="upload-text">{{ $t('upload.upload') }}</p>
      </div>
    </UploadArea>
  </div>
</template>

<script>
import UploadArea from '@/components/upload/index.vue';
import DeletableBox from '@/components/layout/DeletableBox.vue';
import VideoPlayerForLocal from '@/components/video/VideoPlayerForLocal.vue';

export default {
  name: 'UploadWithPreviewer',
  components: {
    UploadArea,
    DeletableBox,
  },
  props: {
    urls: { // 图片集合
      type: Array,
      default: () => [],
    },
    width: {
      type: Number,
      default: 100,
    },
    height: {
      type: Number,
      default: 100,
    },
    accept: {
      type: String,
      default: '',
    },
    multiple: {
      type: Boolean,
      default: false,
    },
    maxSize: {
      type: Number,
      default: 100 * 1024 * 1024,
    },
    max: { // 上传文件最大数量
      type: Number,
      default: 1,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      fileList: [],
    };
  },
  watch: {
    urls: {
      handler(arr) {
        this.fileList = arr.map(item => {
          return { file: '', url: item };
        });
      },
      immediate: true,
    },
  },
  methods: {
    handleUploadFile(files) {
      if (!Array.isArray(files)) files = [files];
      this.fileList.push(...files.map(file => this._formatFileItem(file)));
      if (this.fileList.length >= this.max) {
        this.fileList = this.fileList.slice(0, this.max);
        // this.$message.warning(`最多上传${this.max}文件`);
      }
      this.$emit('change', this.fileList);
    },
    // 删除上传的图片
    handleDeleteFile(i) {
      this.fileList.splice(i, 1);
      this.$emit('change', this.fileList);
    },
    // 处理放大图片功能
    async handlePreviewImage(sourceUrl) {
      await this.$Dialog.dialogify({
        is: VideoPlayerForLocal,
        props: {
          sourceUrl,
        },
      }, {
        props: {
          fullscreen: true,
          width: '100%',
          height: '100%',
          top: '0',
          bottom: '0',
          flex: 'column',
          center: true,
        },
      });
    },
    _formatFileItem(file) {
      return {
        file,
        url: window.URL.createObjectURL(file),
      };
    },
  },
};
</script>

