  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  height: 100%;
  color: #7f7f7f;
  border: 1px dashed #ccc;
  border-radius: 4px;
  transition: all linear 150ms;
  &:hover {
    color: #2878ff;
    border-color: #2878ff;
  }
  .upload-text {
    margin-top: 5px;
    font-size: 12px;
    font-weight: 400;
  }
