<template>
  <div class="model-demo">
    <h2 class="model-demo-title" :style="titleStyle">{{ $t('material.info.attachment_models_caption') }}</h2>
    <ul class="model-demo-list">
      <li
        v-for="(model, i) in models"
        :key="i"
        class="model-demo-item">
        <SelectableBox
          :selected="model.selected"
          :width="164" :height="122"
          :disabled="disabled"
          @preview="handlePreview(model.id)"
          @change="e => changeModel(i, e)">
          <el-image :src="model.cover" fit="contain" />
        </SelectableBox>
        <p class="model-demo-name">Model {{ i + 1 }}</p>
      </li>
    </ul>

    <!-- 截图使用 -->
    <ModelPurePreviewer
      v-if="currentId"
      mode="modelMaterial"
      :modelId="currentId"
      :materialId="materialId"
      :initModelStatus="false"
      style="position: absolute; z-index: -1;opacity:0;"
      ref="modelPreviewer" />
  </div>
</template>

<script>
import axios from '@/utils/axios';
import delay from '_/delay';

import SelectableBox from '@/components/layout/SelectableBox';
import ModelPurePreviewer from '@/views/model/preview/ModelPurePreviewer';

const getMaterialDemoModels = () => {
  return axios({
    method: 'get',
    url: '/bff-center/showCommon/modelSearch/getDisplayModelIds',
  });
};

export default {
  components: {
    SelectableBox,
    ModelPurePreviewer,
  },
  props: ['assetsInfo', 'materialId', 'titleStyle', 'disabled'],
  mounted() {
    this._generateModelData();
    localStorage.setItem('modelCovers', JSON.stringify({}));
  },
  data() {
    return {
      models: [],
      modelIds: [],
      currentId: '',
    };
  },
  methods: {
    // 获取选择框的选择项（外部调用）
    async getSelectResult() {
      // 第一，判断是否有选中的模型
      const selectedModels = this.models.filter(model => model.selected);
      if (!selectedModels.length) return [];

      // 先判断接口返回的数据是否已存在 cover
      selectedModels.forEach(model => {
        if (model.modelCover && model.modelCover.includes('http')) {
          model.modelCover = model.modelCover.split('?')[0].split('/').slice(-2).join('/');
        }
      });
      const missModels = selectedModels.filter(model => model.modelCover === '');
      if (missModels.length === 0) return selectedModels;

      // 再判断缓存是否有已存在（模型预览默认截取）
      const coverObj = JSON.parse(localStorage.getItem('modelCovers'));
      selectedModels.forEach(model => {
        if (!model.modelCover) {
          model.modelCover = coverObj[model.id];
        }
      });
      if (missModels.length === 0) return selectedModels;

      // 最后再调用D4加载模型,获取对应的截图
      for (let i = 0; i < selectedModels.length; i++) {
        const model = selectedModels[i];
        if (!model.modelCover) {
          this.currentId = model.id;
          // await this.$nextTick();
          await delay(500);
          model.modelCover = await this.$refs.modelPreviewer.reloadModel(model.id);
        }
      }

      return selectedModels;
    },
    // 打开鞋模型弹框
    async handlePreview(id) {
      await this.$Dialog.dialogify({
        is: ModelPurePreviewer,
        props: {
          modelId: id,
          materialId: this.materialId,
          mode: 'modelMaterial',
        },
      }, {
        props: {
          flex: 'column',
          width: '580px',
          height: '500px',
        },
      });
    },
    // 获取默认展示三个模型ids,并调整数据结果
    async _generateModelData() {
      this.modelIds = (await getMaterialDemoModels()).data;
      this.models = this.modelIds.map((model) => {
        return {
          id: model.id,
          selected: false,
          cover: model.curtailImg,
          modelCover: '',
        };
      });

      // 通过筛选面料信息中选择模型的数据，同步选中的状态
      if (this.assetsInfo && this.assetsInfo?.models) {
        this.assetsInfo.models.forEach(item => {
          this.models.forEach(model => {
            if (item.id === model.id) {
              model.selected = true;
              model.modelCover = item.cover;
            }
          });
        });
      }
    },
    changeModel(i, e) {
      this.models[i].selected = e;
      this.$closest('MaterialInfo') && (this.$closest('MaterialInfo').saveBtnDisabled = false);
    },
  },
};
</script>

<style lang="scss" scoped>
.el-image {
  width: 100%;
  height: 100%;
}

.model-demo {
  .model-demo-title {
    margin-bottom: 10px;
    line-height: 20px;
    font-size: 14px;
    font-weight: 400;
    color: #606060;
  }
  .model-demo-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    .model-demo-name {
      margin: 10px 0;
      text-align: center;
      font-size: 14px;
      color: #333;
    }
  }
}
</style>
