
























import { defineComponent, ref, watch } from '@vue/composition-api';

import IconMagnify from '@/components/icon/IconMagnify.vue';

export default defineComponent({
  name: 'SelectedPreviewer',
  components: { IconMagnify },
  props: {
    width: {
      type: Number,
      default: 100,
    },
    height: {
      type: Number,
      default: 100,
    },
    selected: {
      type: Boolean,
      default: false,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
  },
  setup(props, { emit }) {
    const selectedStatus = ref(false);

    function toggleSelectedStatus() {
      if (props.disabled) return;
      selectedStatus.value = !selectedStatus.value;
      emit('change', selectedStatus.value);
    }

    watch(() => props.selected, (val) => {
      selectedStatus.value = val;
    }, {
      immediate: true,
    });

    return { selectedStatus, toggleSelectedStatus };
  },
});
