

import { defineComponent, onMounted, ref } from '@vue/composition-api';
import axios from '@/api/axios';
import AttachmentImage from '@/views/material/info/components/AttachmentImage.vue';
import AttachmentVideo from '@/views/material/info/components/AttachmentVideo.vue';
import ModelsSelector from '@/views/material/info/components/ModelsSelector.vue';
import delay from '_/delay';
import i18n from 'i18next';
import GoodsInfo from './components/GoodsInfo.vue';
import GoodsBase from './components/GoodsBase.vue';
import GoodsRichText from './components/GoodsRichText.vue';
import FormSubmitButtons from './components/FormSubmitButtons.vue';


const goodsEdit = defineComponent({
  name: 'GoodsEdit',
  // components: {
  //   GoodsInfo,
  //   GoodsBase,
  //   GoodsRichText,
  // },
  props: {
    id: {
      type: String,
      default: '',
    },
    materialId: {
      type: String,
      default: '',
    },
  },

  render() {
    const {
      material, cancel, submit, materialId, titleStyle,
    } = this as InstanceType<typeof goodsEdit>;
    return (
      <main style='flex:1;' class='flex-column flex-vertical-center' >
        <GoodsInfo
          class="goods-edit-section"
          ref="goodsInfo"
          material={material}>
          <section style="width:520px;">
            {material.imgJson?.models
              ? <ModelsSelector ref="modelsSelector"
                titleStyle={titleStyle}
                materialId={materialId}
                assetsInfo={{ models: material.imgJson?.models || [] }}>
              </ModelsSelector>
              : null
            }
          </section>
          <section class="padding-next">
            {material.imgJson?.images
              ? <AttachmentImage ref="attachmentImage"
                titleStyle={titleStyle}
                assetsInfo={{ images: material.imgJson.images || [] }}>
              </AttachmentImage>
              : null
            }
          </section>
          <section class="padding-next">
            {material.imgJson?.videos
              ? <AttachmentVideo ref="attachmentVideo"
                titleStyle={titleStyle}
                assetsInfo={{ videos: material.imgJson?.videos || [] }}>
              </AttachmentVideo>
              : null
            }
          </section>
        </GoodsInfo>
        <GoodsBase class="goods-edit-section" ref="goodsBase" material={material}></GoodsBase>
        <GoodsRichText class="goods-edit-section" value={material.detail} ref="goodsRichText" material={material}></GoodsRichText>
        <section class="goods-submit-section">
          <FormSubmitButtons size="medium"
            btn-style={{ width: '90px' }}
            onCancel={cancel}
            onPublish={submit.bind(this, true)}
            onSubmit={submit}>

          </FormSubmitButtons>
        </section>
      </main>
    );
  },
  // @ts-ignore
  setup({ id, materialId }, { root, refs }) {
    // 面料详细信息
    const material = ref<any>({});
    // 面料富文本信息
    // const goodsDetail = ref('');

    const titleStyle = {
      'font-size': '18px',
      margin: '0 0 20px 0',
      'line-height': '24px',
      'font-weight': 'bold',
      color: '#333',
    };

    function cancel() {
      root.$router.back();
    }

    async function submit(isPublish = false) {
      const loading = root.$loading();
      try {
        // loading.value = true;
        // 获取材质图片的结果
        const images = await refs.attachmentImage.getImageOssPaths();

        const materialUpdateInfo = await refs.goodsInfo.validateForm();

        if (!materialUpdateInfo) return;
        const { rText } = refs.goodsRichText;

        // 获取模型选择的结果
        let models = await refs.modelsSelector.getSelectResult();
        models = models.map(model => {
          return { id: model.id, cover: model.modelCover };
        });


        // 获取模型选择的结果
        const videos = await refs.attachmentVideo.getVideoOssPaths();

        const newImageJson = { ...material.value.imgJson };
        type ValueOf<T> = T[keyof T];
        Object.keys(newImageJson).forEach((key: ValueOf<typeof newImageJson>) => {
          if (typeof newImageJson[key] === 'string' && newImageJson[key].includes('http')) {
            newImageJson[key] = newImageJson[key].split('?')[0].split('/').slice(-2).join('/');
          }
        });

        const reqData = {
          materialId,
          ...materialUpdateInfo,
          tags: materialUpdateInfo.tags?.map((tag: any) => ({ tag })) || [],
          detail: rText,
          imgJson: {
            ...newImageJson, models, images, videos,
          },
        };
        if (isPublish) {
          reqData.publishStatus = true;
        }

        await axios({
          method: 'post',
          url: '/mall/material/operate/edit',
          data: reqData,
        });
        if (isPublish) {
          root.$message.success(i18n.t('submit_success'));
        } else {
          root.$message.success(i18n.t('modify_success'));
        }
        await delay(1000);
        root.$router.back();
      } catch (e) {
        root.$errHandler(e);
      } finally {
        loading.close();
      }
    }

    onMounted(async () => {
      const materialOrigin = (await axios.get('/mall/material', {
        params: {
          id,
        },
      })).data;
      // 处理单位类型的数据类型 number 转为 string; (否则编辑页面的表单校验会提示转为字符串类型)
      Object.keys(materialOrigin).forEach(key => {
        if (materialOrigin[key] === null) materialOrigin[key] = '';
        else if (typeof materialOrigin[key] === 'number') {
          materialOrigin[key] = materialOrigin[key].toString();
        }
      });
      materialOrigin.imgJson.images = materialOrigin.imgJson?.images || [];
      materialOrigin.imgJson.videos = materialOrigin.imgJson?.videos || [];
      materialOrigin.imgJson.models = materialOrigin.imgJson?.models || [];

      material.value = materialOrigin;
    });

    return {
      material,
      titleStyle,

      cancel,
      submit,
    };
  },
});
export default goodsEdit;

