















































































































import {
  defineComponent, onMounted, ref, watch,
} from '@vue/composition-api';
import VueInputTag from 'vue-input-tag';
import { arrayTranslate } from '@/i18n';
import { ElForm } from 'element-ui/types/form';

import validator, { validateFormFields } from '@/utils/validators/rules-validation';
import DictionarySelectorNew from '@/components/dictionary/DictionarySelectorNew.vue';
import {
  categoryName2surfaceTechnics, categoryName2MaterialsTechnology, designContent, applicableProductLine,
  MaterialsTechnology, SurfaceTechnics,
} from '@/models/Material';

import IconQuestion from '@/components/icon/IconQuestion.vue';
import MaterialTypeCascade from '@/components/MaterialTypeCascade.vue';
import { axios } from '@/utils/axios';

// 类目详情
export const fetchMaterialDetail = async (id: string) => {
  return (await axios({
    method: 'get',
    url: `/show/category/detail?id=${id}`,
  })).data;
};


const materialFromRules = {
  materialName: [
    validator.requirement(),
    validator.charLengthMax(512),
  ],
  samplePrice: [
    validator.numberPositive(),
    validator.charLengthMax(20),
  ],
  materialsTechnology: validator.requirement(),
  applicableProductLine: validator.requirement(),
  designContent: validator.requirement(),
  surfaceTechnics: validator.requirement(),
};


export default defineComponent({
  components: {
    DictionarySelectorNew,
    VueInputTag,
    IconQuestion,
    MaterialTypeCascade,
  },
  props: {
    material: {
      type: Object,
      required: true,
    },
  },
  setup(props, { root }) {
    const materialGoods = ref<any>({
      materialsTechnology: [],
      applicableProductLine: [],
      designContent: [],
      surfaceTechnics: [],
    });

    // 类别节点数组
    const categoryArr = ref<any[]>([]);
    // 获取分类信息
    const parentIds = ref<string[]>([]);

    const typeCascade = ref(null);

    // 材料种类
    const materialsTechnologyList = ref<MaterialsTechnology[]>([]);
    // 表面工艺
    const surfaceTechnicsList = ref<SurfaceTechnics[]>([]);

    // 图案内容
    const designContentList = designContent;
    // 适用产品线
    const applicableProductLineList = applicableProductLine;

    watch(() => props.material, async (v) => {
      materialGoods.value = {
        materialName: v.materialName,
        samplePrice: v.samplePrice,
        samplePriceUnit: v.samplePriceUnit,
        samplePriceNumber: v.samplePriceNumber,
        tags: v.tags.map(tag => tag.tag),
        category: v.category || '',
        materialsTechnology: v.materialsTechnology ? v.materialsTechnology.split(',') : [],
        applicableProductLine: v.applicableProductLine ? v.applicableProductLine.split(',') : [],
        designContent: v.designContent ? v.designContent.split(',') : [],
        surfaceTechnics: v.surfaceTechnics ? v.surfaceTechnics.split(',') : [],
      };

      if (props.material.category) {
        const res = await fetchMaterialDetail(props.material.category);
        parentIds.value = [...res.parentIds.slice(), props.material.category];
      }
    });
    const refsForm = ref<ElForm>(null);
    // 检测表单是否通过校验
    function checkFormValidation() {
      // if (typeof (refsForm.value as any).validate === 'function') {
      //   (refsForm.value as any).validate((valid: boolean) => {
      //     emit('update:saveDisabled', !valid);
      //   });
      // }
    }

    // 提交表单（外部调用）
    async function validateForm() {
      Object.keys(materialGoods.value).forEach((prop) => {
        if (typeof materialGoods.value[prop] === 'string') {
          materialGoods.value[prop] = materialGoods.value[prop].trim();
        }
      });
      const formData = JSON.parse(JSON.stringify(materialGoods.value));
      const pickedKeys = ['designContent', 'applicableProductLine', 'surfaceTechnics', 'materialsTechnology'] as const;

      pickedKeys.forEach(k => {
        console.info(k);
        console.info(materialGoods.value[k]);
        formData[k] = materialGoods.value[k].join();
      });
      try {
        if (refsForm.value) {
          await validateFormFields(refsForm.value);
          // emit('validate', materialGoods.value);
        }
        return formData;
      } catch (err) {
        // console.warn(err);
        root.$errorHandler(err);
        return undefined;
      }
    }

    watch(() => categoryArr.value, async (v: any[], oldV) => {
      if (v[1].name === oldV[1]?.name) return;
      materialsTechnologyList.value = categoryName2MaterialsTechnology[v[1].name as '织物' | '人造革' | '真皮'];
      surfaceTechnicsList.value = categoryName2surfaceTechnics[v[1].name as '织物' | '人造革' | '真皮'];
      if (oldV.length > 0) {
        materialGoods.value.materialsTechnology = [];
        materialGoods.value.surfaceTechnics = [];
      }
    });


    return {
      materialGoods,
      rules: materialFromRules,
      refsForm,

      checkFormValidation,
      validateForm,

      parentIds,
      categoryArr,
      typeCascade,
      materialsTechnologyList,
      surfaceTechnicsList,
      designContentList,
      applicableProductLineList,
      arrayTranslate,
    };
  },
});
